.principal {
    text-align: center;
    padding: 100px;
    color: #140b0b;
}

.padre{
    display: flex;
    flex-direction: row;
}

.projects p{
    margin-top: 40px;
    text-align: center;
    font-size: 20px;
}

.contenedor {
    width: 100%;
    margin-top: 40px;
    margin-right: 40px;
}

.cuadro {
    position: relative;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 2px solid #5DADE2;
    background-color: #F7F9F9;
    height: 360px;
    width: 260px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    transition: all .3s;
}

.cuadro img {
    border-radius: 10px;
    width: 100%;
    height: 60%;
}

.cuadro a{
    position: absolute;
    top: 360px;
    margin: 0 2rem;
    left: 60px;
    text-decoration: none;
    padding: 10px;
    background-color: #000;
    color: #F7F9F9;
    border-radius: 20px;
    font-size: 20px;
    transition: all .5s;
}


/* ----------------------------Media Querys--------------------- */

@media screen and (max-width: 1100px){
 .contenedor{
  margin-left: -50px;
    margin-right: 40px;
 } 
}

@media screen and (max-width: 991px){
    .contenedor{
        margin-left: 0px;
        margin-right: auto;
    } 
    .cuadro{
        margin-left: -50px;
    }
    .cuadro a{
        top: 360px;
    }
   }


   @media screen and (max-width: 757px){
    .cuadro{
        height: 240px;
        width: 200px;
    }
    .cuadro a{
        margin-top: -120px;
        left: 0;
    }
}

@media screen and (max-width:400px){
.contenedor{
    margin-left: 10px;
    margin-right: 20px auto;
}

.cuadro{
    height: 139px;
    width: 100px;

}
.cuadro a{
    margin-top: -220px;
    left: 0;
    padding: 1px;
    text-align: center;
}
}
   







