    text-align: center;
    /* margin-top: 120px; */
    
}

.formulario {
    margin-top: 4px;
    background-color: #000;
    color: #F7F9F9;
    width: 100%;
    height: 400px;
    padding-top: 10px;
    padding-bottom: 500px;
}

.formulario form{
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    color: #F7F9F9;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    padding: 10px;
    font-size: 26px;
    width: 40%;
    text-align: center;
    margin: 0 auto;
}
.formulario form .Block{
grid-column: 1/3;
}

.formulario input{
    border: none;
    font-size: 18px;
    width: 80%;
    margin-top: 20px;
    padding: 5px;
}
.formulario label {
    margin: 10px 0;
}


.formulario textarea {
    border: none;
    border-radius: 5px;
    padding: 10px;
    margin-top: 5px;
    font-size: 18px;
    justify-content: center;
    width: 180%;
    margin-left: 40px;
    height: 260px;
}

.btn-send{
    border-radius: 10px;
    color: #F7F9F9;
    background-color: #5DADE2;
    border: none;
    width: 30%;
    margin: 0 auto;
    outline: none;
    grid-column: 1/3;
    font-size: 20px;
    transition: all .5s;
    cursor: pointer;
    border: 2px solid #F7F9F9;
}
