.Skills{
    background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
    padding: 68px;
    text-align: center;
}

.titulo2{
    color: #140b0b;
   margin-top: 10px;
}

.habilidades{
    margin-top: 70px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
}

.habilidades img{
    width: 10%;
    height: 10%;
}

.habilidades p{
    display: flex; 
    font-size: 30px;
    text-align: center;
    color: white;
    margin-top: -40px;
    margin-left: -290px;
    padding: 3px;
}

@media screen and (max-width: 1600px){
.habilidades p{
    margin-left: -230px;
}
}

@media screen and (max-width: 1200px){
.habilidades p{
margin-left: -150px;
}
}

@media screen and (max-width: 900px){
.habilidades p{
 margin-left: -110px;
}
}