.portada1{
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    padding: 40px;
    display: flex;
    justify-content: center;
}

.flex1 {
    display: flex;
    flex-direction: column;
    margin-top: 60px;
    align-items: center;
    justify-content: space-evenly;
  }

.imagen5{
    width: 380px;
    height: 380px;
    margin-top: 50px;
}



.titulo {
    text-align: center;
    justify-content: center;
    color: #000;
    margin-top: -120px;
}
.parafo {
    text-align: center;
    margin-top: -150px;
    font-size: 20px;
    color: black;
    padding-left: 40px;
}
/* ----------------------------Media Querys--------------------- */

/*@media screen and (max-width: 1100px){*/


@media screen and (max-width: 991px){
    .imagen5{
   margin-left: 0;
   display: flex;

}
    .titulo{
      margin-top: 10px;
      margin-right: -190px;
      margin-left: -150px;
    }
    .parafo{
     margin-top: 30px;
    }
}


@media screen and (max-width:600px){
    .portada1{
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width:400px){
.imagen5{
    width: auto;
}
}
